<template>
  <div class="allPage" v-loading="loading">
    <div class="w">
      <div>
        <div v-if="inputbox">
          <div style="margin-top:30%;">
            <el-form label-width="80px">
              <el-form-item label="/">
                <el-input v-model="number"></el-input>
              </el-form-item>
            </el-form>
          </div>
            <el-button @click=" testBeforeAmnmt(number)"></el-button>
         </div>
        <div v-if="student">
          <el-form ref="form1" :model="form1" label-width="80px" >
            <el-form-item label="">
              <el-input v-model="form1.student_name"></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_class" ></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-select v-model="form1.student_department" placeholder="" style="width:326px;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="  " value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_major"></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-select v-model="form1.student_from" placeholder="" style="width:326px;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_id" ></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_address"></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_birthplace"></el-input>

            </el-form-item>

            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="" v-model="form1.student_join_time" style="width:326px;"></el-date-picker>
              </el-col>
            </el-form-item>

            <el-form-item label="">
              <el-radio-group v-model="form1.student_sex">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="">
              <el-radio-group v-model="form1.student_political_status">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form1.student_idcard"></el-input>
            </el-form-item>
          </el-form >
          <div>
            <el-button type="primary" @click="testBeforeSubmitTo(form1)"></el-button>
            <el-button @click="returnToPage"></el-button>
          </div>
        </div>
        <div v-if="teacher">
          <el-form ref="form1" :model="form2" label-width="80px">
            <el-form-item label="">
              <el-input v-model="form2.teacher_name"></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form2.teacher_id" ></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-select v-model="form2.teacher_department" placeholder="" style="width:326px;">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="">
              <el-input v-model="form2.teacher_major"></el-input>
            </el-form-item>



            <el-form-item label="">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="" v-model="form2.teacher_join_time" style="width:326px;"></el-date-picker>
              </el-col>
            </el-form-item>

          </el-form>
          <div>
            <el-button type="primary" @click="testBeforeSubmitTo(form2)"></el-button>
            <el-button @click="returnToPage"></el-button>
          </div>
        </div>
        <!--<div>-->
          <!--<el-table-->
              <!--v-loading="loading2"-->
              <!--element-loading-text=""-->
              <!--element-loading-spinner="el-icon-loading"-->
              <!--element-loading-background="rgba(0, 0, 0, 0.8)"-->
              <!--style="width: 100%">-->
            <!--</el-table>-->
        <!--</div>-->

      </div>
      </div>
  </div>
</template>

<script>
  import {amemtMsg,sentMsg} from "../../axios/api";
  import {testBeforeSubmit,testBeforeSubmit2} from "../../js/linkStudentMsg"

    export default {
        name: "amentMsg",
      data(){
          return{
            loading:false,
            // loading2:false,
            number:'',
            student:false,
            teacher:false,
            inputbox:true,
            formed:{},
            form1:{
              _student_id: "",
              student_name: "",
              student_class: "",
              student_department: "",
              student_id: "",
              student_major: "",
              student_address:"",
              student_birthplace:"",
              student_join_time:"",
              student_sex:"",
              student_from:"",
              student_political_status:"",
              student_idcard:"",
              usertype:1

            },
            form2:{
              _teacher_id:'',
              teacher_name:'',
              teacher_id:'',
              teacher_department:'',
              teacher_major:'',
              teacher_join_time:'',
              usertype:2
            },
        }
      },

      methods:{

        //  
        testBeforeAmnmt(number){
          if(this.number===''){
            alert("")
            return
          }if(this.number.length==9){
            this.amemtMsgTo(this.number)
            return
          }if(this.number.length==10){
            this.amemtMsgTo(number)
            return
          }
          alert("id")
          return
        },

        //
        returnToPage(){
            this.number="",
            this.teacher=false,
            this.student=false,
            this.inputbox=true
        },

        //
        amemtMsgTo(number) {
          amemtMsg(number).then(amemtresult => {
            console.log(amemtresult);
            if(amemtresult.data.length===0){
              this.$alert('', '', {   //
                confirmButtonText: '',
              });
              return
            }
            if(amemtresult.data[0].usertype==2){
              this.teacher=true;
              this.inputbox=false;
              this.teacherMsg(amemtresult)
            }else{
              this.studentMsg(amemtresult);
              this.student=true;
              this.inputbox=false;
            }
          }).catch(error => {
            console.log(error.response)
          })
        },

        //
        studentMsg(amemtresult) {
          console.log(amemtresult);
          this.form1 = amemtresult.data[0]
        },

        //  
        teacherMsg(amemtresult){
          console.log(amemtresult);
          this.form2 = amemtresult.data[0]
        },

        //
        testBeforeSubmitTo(){
          if(this.number.length===9){
            let that= this;
            testBeforeSubmit(that);
             this.form1._student_id=this.form1.student_id;
             this.formed=this.form1;
            this.sentMsgTo(this.formed)
          }else{
            let that= this;
            testBeforeSubmit2(that);
             this.form2._teacher_id=this.form2.teacher_id;
            this.formed=this.form2;
            this.sentMsgTo(this.formed)
          }
        },

      //  
        sentMsgTo(){
          let that=this;
          sentMsg(this.formed).then(sentresult => {
            console.log(sentresult);
            that.loading = true;
            if(sentresult.data.length===8){
              that.loading=false;
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration:'2000'
              });
            }else{
              that.loading=false;
              this.$notify.error({
                title: '',
                message: '',
                duration:'2000'
              });
            }
          }).catch(error => {
            console.log(error.response)
            this.$notify.error({
              title: '',
              message: '',
              duration:'2000'
            });
          })
        }
      }
    }





</script>

<style scoped>
.allPage {
  height:100%;
  width:100%;
}
  .w {
    width:40%;
    margin:0 auto;
  }
</style>



