<template>
  <div class="allPage">
    <div class="systemState">
      <el-form ref="form" :model="formData2" label-width="80px">
        <el-form-item label="" >
          <el-switch v-model="formData2.system_state" @change="systemStateChange"></el-switch>
        </el-form-item>
      </el-form>
    </div>
    <div class="form">
    <el-form :model="formData" :rules="rules" ref="formData" label-width="100px">
      <el-form-item label="" prop="course_name">
        <el-input v-model="formData.course_name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="teacher_name">
        <el-input v-model="formData.teacher_name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="course_week">
        <el-input v-model="formData.course_week" autocomplete="off" placeholder="1-16"></el-input>
      </el-form-item>
      <el-form-item label="" prop="course_student_num">
        <el-input type="number" v-model.number="formData.course_student_num" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="" prop="course_range_college">
        <el-select v-model="formData.course_range_college" placeholder="" @change="collegeChange" style="width:100%;">
          <el-option v-for="(item ,index) in college_item"  :key="index" :value="index+1" :label="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="course_range_major">
        <el-select v-model="formData.course_range_major" placeholder=""  style="width:100%;">
          <el-option v-for="(item ,index) in major_item[college_value]" :key="index"  :value="index+1" :label="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('formData')"></el-button>
        <el-button @click="resetForm('formData')"></el-button>
      </el-form-item>
    </el-form>
    </div>
  </div>
</template>

<script>
  import {get_num_code} from "../../axios/api";
  import {open_elective_system} from "../../axios/api";
  import {off_elective_system} from "../../axios/api";
  import {get_elective_system_state} from "../../axios/api";
  import {insert_new_class} from "../../axios/api";

  export default {
      name: "addClass",
      data(){
          return{
            formData2:{
              system_state:false,
            },
            formData: {
              course_name:"",  //
              teacher_name:"", //
              course_week:"",  //
              course_student_num:"", //
              course_range_college:"",  //
              course_range_major:"", //0
            },
            rules: {
              course_name:[
                { required: true, message: ''}
              ],
              teacher_name:[
                { required: true, message: ''}
              ],
              course_week:[
                { required: true, message: ''}
              ],
              course_student_num:[
                { required: true, message: ''},{ type: 'number', message: ''}
              ],
              course_range_college:[
              { required: true, message: ''}
              ]
            },
            college_item:[],//
            major_item:[],//
            college_value:"",
            major_value:"",
          }
      },
      created:function () { //
        //get collage/major Num
        let that=this;
        get_num_code().then(res=>{
          this.college_item=res.data[0];
          for(let i=0;i<res.data[0].length;i++){
            that.major_item.push(res.data[i+1]);
          }
        })
        get_elective_system_state().then(res=>{
          console.log(res.data);
          if(res.data===0){
            this.formData2.system_state=false
          }else if(res.data===1){
            this.formData2.system_state=true
          }
        })

      },
      methods:{
        systemStateChange(){
          if(this.formData2.system_state===false){
            this.offSystem();
            console.log("guanbi")
          }else {
            this.openSystem();
          }
        },
        openSystem(){
          let that =this;
          let cache=that.formData2.system_state;
          open_elective_system().then(res=>{
            console.log(res);
            if(res.data===1){
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration:'2000'
              });
              that.formData2.system_state=true;
            }else{
              //
              that.formData2.system_state=cache;
              this.$notify.error({
                title: '',
                message: '',
                duration:'2000'
              });
            }
          })
        },
        offSystem(){
          let that =this;
          let cache=that.formData2.system_state;
          off_elective_system().then(res=>{
            if(res.data===1){
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration:'2000'
              });
              that.formData2.system_state=false;
            }else{
              //
              that.formData2.system_state=cache;
              this.$notify.error({
                title: '',
                message: '',
                duration:'2000'
              });
            }
          })
        },
        submitForm(formName) {
          if(!this.formData.course_range_major){
            this.formData.course_range_major=0
          }
          this.$refs[formName].validate((valid) => {
            if (valid) {
              insert_new_class(this.formData).then(res=>{
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration:'2000'
                });
              })
            } else {
              console.log('error submit!!');
              this.$notify.error({
                title: '',
                message: '',
                duration:'2000'
              });
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
        collegeChange(){
          console.log(this.formData.course_range_college)
          this.college_value=this.formData.course_range_college-1
        }
      }
    }
</script>

<style scoped>
.allPage {
 width:100%;
  height:100%;
}
  .systemState {
    position: absolute;
    top:0;
    right:50px;
  }
  .allPage >.form {
    width:30%;
    margin: 40px auto 0;
  }
</style>
