      returnData:
        0 //
        1 //
    postData1:
        systemState:1 //num
        systemState:0 //num
      returnData:
        0 //
        1 //
    
    postData0:
        course_name:""  //
        teacher_name:"" //
        course_week:""  //
        course_student_num:"" //
        course_range_college:""  //
        course_range_major:"" //0
      returnData:
        0 //
        1 //
    
    postData0:
        noData
      returnData: //0.1
        {
          0:{
              1:""
              2:""
              3:""
              ...
            },
          1:{
              1:""
              2:"",
              3:""
              ...
            },
          2:{},
          ...
        }

        //
        [
          [
            "1","2","3","4"...
          ],
          [
            "1","2","3","4"...
          ]
        ]
