<template>
  <div class="allPage">
    <div class="error"
      v-if="error"
    >
      <img src="../../assets/error.png" alt="">
      <div></div>
    </div>
    <div class="elective" v-if="!error" v-loading="loading">
      <div class="title"></div>
      <el-table :data="electiveData">
        <el-table-column width="250" property="course_name" label=""></el-table-column>
        <el-table-column width="250" property="course_week" label=""></el-table-column>
        <el-table-column width="250" property="teacher_name" label=""></el-table-column>
        <el-table-column width="250" property="course_surplus_num" label=""></el-table-column>
        <el-table-column width="250" property="course_student_num" label=""></el-table-column>
        <el-table-column width="250" property="course_state" label=""></el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="180">
          <template slot-scope="scope">
            <!--@click.native.prevent="deleteRow(scope.$index, tableData4)"-->
            <el-button
              type="text"
              size="small"
              @click.native.prevent="choose_class(160121712,scope.$index)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="title"></div>
      <el-table :data="electiveOverData">
        <el-table-column width="250" property="course_name" label=""></el-table-column>
        <el-table-column width="250" property="course_week" label=""></el-table-column>
        <el-table-column width="250" property="teacher_name" label=""></el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="180">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click.native.prevent="cancal_class(160121712,scope.$index)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import {get_elective} from "../../axios/api"
  import {choose_elective} from "../../axios/api";
  import {cancel_elective} from "../../axios/api";

  export default {
        name: "elective",
      data(){
        return{
          electiveData:[],
          electiveOverData:[],
          error:false,//
          loading:false
        }
      },
      created: function() {     //
          let that=this;
           that.loading=true;
          get_elective(160121712).then(res=>{
            // console.log(res.data);
            if(res.data===0){//
              that.loading=false;
              that.electiveData=[];
              that.electiveOverData=[];//
              this.$alert('', '', {   //
                confirmButtonText: '',
              });
              that.error=true;
            }else{
              let elective_courseId=[];//Id
              for(let i=0;i<res.data[1].length;i++){  //Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //Id
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
      },
      methods: {
        choose_class(userId,index){
          let chooseClassId=this.electiveData[index].course_id;
          let that=this;
          that.loading=true;
          choose_elective(userId,chooseClassId).then(res=>{
            console.log(res.data);

            if(res.data===2){//
              get_elective(160121712).then(res=>{
                // console.log(res.data);
                if(res.data===1){//
                  that.loading=false;
                  that.electiveData=[];
                  that.electiveOverData=[];//
                  this.$alert('', '', {   //
                    confirmButtonText: '',
                  });
                  that.error=true;
                }else{
                  let elective_courseId=[];//Id
                  for(let i=0;i<res.data[1].length;i++){  //Id
                    elective_courseId.push(res.data[1][i].course_id)
                  }
                  for(let i = res.data[0].length-1;i>=0;i--){  //Id
                    if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
                  }
                  that.electiveData=res.data[0];
                  that.electiveOverData=res.data[1];
                  that.loading=false;
                }
              })
              this.$alert('', '', {   //
                confirmButtonText: '',
              });
            }else{
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration:'2000'
              });
              let elective_courseId=[];//Id
              for(let i=0;i<res.data[1].length;i++){  //Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //Id
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
        },
        cancal_class(userId,index){
          let cancelClassId=this.electiveOverData[index].course_id;
          let that =this;
          that.loading=true;
          cancel_elective(userId,cancelClassId).then(res=>{
            console.log(res.data);

            if (res.data===3){ //
              that.loading=false;
              this.$alert('', '', {   //
                confirmButtonText: '',
              });
            }else{
              this.$notify({
                title: '',
                message: '',
                type: 'success',
                duration:'2000'
              });
              let elective_courseId=[];//Id
              for(let i=0;i<res.data[1].length;i++){  //Id
                elective_courseId.push(res.data[1][i].course_id)
              }
              for(let i = res.data[0].length-1;i>=0;i--){  //Id
                if(elective_courseId.indexOf(res.data[0][i].course_id)>-1 )res.data[0].splice(i, 1);
              }
              that.electiveData=res.data[0];
              that.electiveOverData=res.data[1];
              that.loading=false;
            }
          })
        }
      }
    }
</script>

<style scoped>
  .error {
    height:100%;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .error div {
    margin-top: 30px;
    font-size:30px;
    font-weight: lighter;

  }

  .error img {
    height: 500px;
  }
  .allPage {
    width:100%;
    height:100%;
  }
.elective {
  width:100%;
  height:100%;

}
  .title {
    font-size: 30px;
    text-align: left;
    padding-left: 30px;
    color:#10aeff;
  }
</style>
