    return_data:
    [ //
      [ //
        {
          course_id:"",//id
          course_name:"",  //
          course_week:"",  //1-57-8
          teacher_name:"", //
          course_surplus_num:"", //
          course_student_num:"", //
          course_state:""        //
        },{
          course_id:"",
          course_name:"",
          course_week:"",
          teacher_name:"",
          course_surplus_num:"",
          course_student_num:"",
          course_state:""
        }
      ],
      [  //
        {
          course_id:"",//id
          course_name:"",  //
          course_week:"",  //
          teacher_name:"",  //
        },{
          course_id:"",
          course_name:"",
          course_week:"",
          teacher_name:"",
        }
      ]
    ]

    postData1:
        userId

        success:return return_data
        fail:return 1 //

    postData2:
        userId
        chooseClassId

        success:return return_data
        fail:return 2 //

    postData3:
        userId
        cancelClassId

        success:return return_data
        fail:return 3 //

-->



