<template>

  <div class="main">

    <header>
      <div class="top">
        <p>  </p>
        <el-button @click="goToLoginPage"></el-button>
      </div>
      <footer>
        <p>&nbsp;:&nbsp;{{student_name}}&nbsp;&nbsp;&nbsp;
          &nbsp;:&nbsp;{{student_class}}&nbsp;&nbsp;&nbsp;
          &nbsp;:&nbsp;{{student_department}}&nbsp;&nbsp;&nbsp;
          &nbsp;:&nbsp;{{student_major}}&nbsp;&nbsp;&nbsp;
          &nbsp;:&nbsp;{{student_id}}</p>
      </footer>
    </header>
    <div class="kongbai"></div>
    <aside>
      <el-button class="sidebarbox" @click="changeIsShow('info')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('manage')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('test')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('schedule_query')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
    </aside>
    <div class="mainbox">
      <div class="school_news"></div>
      <div class="person_news"></div>
    </div>
    <div class="mainbox">
      <studentInfo  v-if="type==='info'"></studentInfo>
      <testQuery  v-if="type==='test'"></testQuery>
      <elective v-if="type==='manage'"></elective>
      <schedule_query v-if="type==='schedule_query'"></schedule_query>
      <!--<schedule_query v-if="type==='schedule_query'"></schedule_query>-->
      <!--<query v-bind:class={mainbox:show==='query'}></query>-->
      <!--<query2 v-bind:class={mainbox:show==='query2'}></query2>-->
    </div>

    <div class="back">
      <el-button @click="goToLoginPage"></el-button>
    </div>
  </div>

</template>


<script>

  import studentInfo from './studentInfo'
  import testQuery from './testQuery'
  import elective from './elective'
  import schedule_query from './schedule_query'

  export default {
    name: "mainPage",

    data() {
      return {
        student_name: "",
        student_class: "",
        student_department: "",
        student_id: "",
        student_major: "",
        type:"",
      }
    },

    mounted(){
      this.studentMsg();
      console.log("createdDone");
    },
    components:{
      'testQuery':testQuery,
      'studentInfo':studentInfo,
      'elective':elective,
      'schedule_query':schedule_query,
    },
    methods: {

      //
      studentMsg() {
        const result = JSON.parse(localStorage.getItem('result'));
        // console.log(result);
        this.student_name = result.data[0].student_name;
        this.student_class = result.data[0].student_class;
        this.student_department = result.data[0].student_department;
        this.student_major = result.data[0].student_major;
        this.student_id = result.data[0].student_id;
      },

      //
      changeIsShow(compoment_name){
        this.type = compoment_name

      },

      //
      goToLoginPage() {
        if (confirm("")) {
          this.$router.push({path: '/'})
        }
        else {
          return
        }
      },
    },

  }
</script>

<style scoped>
  @import "../main_style.css";


</style>



