<template>
    <div class="main">
      <header>
        <div class="top">
          <p> ---</p>
          <el-button @click="goToLoginPage"></el-button>
        </div>
        <footer>
          <p>&nbsp;&nbsp;{{teacher_name}}&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;{{teacher_major}}&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;{{teacher_id}}&nbsp;&nbsp;</p>
        </footer>
      </header>
      <div class="kongbai"></div>
      <aside>
        <el-button class="sidebarbox" @click="changeIsShow('info')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
        <el-button class="sidebarbox" @click="changeIsShow('manage')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      </aside>

      <div class="mainbox">
        <div class="school_news"></div>
        <div class="person_news"></div>
      </div>
      <div class="mainbox">
        <updataMark v-if="type=='info'"></updataMark>
        <addClass v-if="type=='manage'"></addClass>
        <!---->
      </div>
      <div class="back">
        <button @click="goToLoginPage"></button>
      </div>
    </div>

</template>

<script>
  import updataMark from './updataMark'
  import addClass from './addClass'
    export default {
        name: "teacherPage",
      data(){
          return{
            teacher_department: "",
            teacher_id: "",
            teacher_join_time: "",
            teacher_major: "",
            teacher_name: "",
            type:""

          }
      },
      //
      mounted(){
        this.teacherMsg();
        console.log("createdDone");
      },
      components:{
        updataMark:updataMark,
        addClass:addClass
      },
      methods:{

      //    
        teacherMsg(){
          const result = JSON.parse(localStorage.getItem('result'));
          console.log(result)
            this.teacher_department=result.data[0].teacher_department;
            this.teacher_id=result.data[0].teacher_id;
            this.teacher_join_time=result.data[0].teacher_join_time;
            this.teacher_major=result.data[0].teacher_major;
            this.teacher_name=result.data[0].teacher_name
        },

        //
        changeIsShow(compoment_name){
          this.type = compoment_name

        },

        //
        goToLoginPage() {
          if (confirm("")) {
            this.$router.push({path: '/'})
          } else {
            return
          }
        }


      }
    }
</script>

<style scoped>
  @import "../main_style.css";


</style>



