<template>
  <div class="hello">
    <div class="box_1">
      <el-button @click="switchUpdate('stu')"></el-button>
      <el-button @click="switchUpdate('tea')"></el-button>
    </div>
    <div class="box_2">
      <p v-if="type=='stu'"></p>
      <p v-if="type=='tea'"></p>
      <div v-if="type.length!==0">
        <form>
          <input type="file" @change="getFile($event)">
          <button class="button button-primary button-pill button-small" @click="submit($event)"></button>
        </form>
      </div>
    </div>
    <!--<div>-->
      <!--<form>-->
        <!--<input type="file" @change="getFile($event)">-->
        <!--<button class="button button-primary button-pill button-small" @click="submit($event)"></button>-->
      <!--</form>-->
    <!--</div>-->
  </div>

</template>

<script>
  import {sentCsv} from "../../axios/api";



  export default {
    name: 'postCsv',
    data() {
      return {
        type:'' ,
        file: ''
      }
    },
    methods: {
      //
      switchUpdate(userType){
        this.type = userType
      },


      //
      getFile: function (event) {
        this.file = event.target.files[0];
        console.log(this.file);
      },
      submit: function (event) {
        event.preventDefault();
        let formData = new FormData();
        formData.append("file", this.file);
         this.postCsvTo(formData,this.type)
        },
      //
      postCsvTo(formData,type){
        let that=this;
        sentCsv(formData,type)
          .then(function (response) {
            that.$notify({
              title: '',
              message: '',
              type: 'success',
              duration:'2000'
            });
            console.log(response);
            window.location.reload();
          })
          .catch(function (error) {
            that.$notify.error({
              title: '',
              message: '',
              duration:'2000'
            });
            console.log(error);
            window.location.reload();
          });
      },

    }
  }
</script>

<style scoped>
.box_1,box_2 {
  margin: 30px 0 30px 0;

}
</style>



