<template>
  <div class="allPage">
    <div class="index"
         v-loading="loading"
    >
      <el-alert
        v-if="alert_ctrl"
        title="~~"
        type="warning"
        show-icon
        :closable="false"
        style="position: absolute;top:0;"
      >
      </el-alert>
      <div class="top">
        <el-select v-model="value" placeholder=""  @change="pickerChange">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <el-button @click="findTimeTable" type="success" icon="el-icon-check" circle :disabled='search_show' style="margin-left: 20px;"></el-button>
      </div>
      <div class="error" v-if="alert_ctrl">
        <div></div>
        <img src="../../assets/noclass.jpg" alt="">
      </div>
      <div class="cen">
        <div class="list">
          <div class="title"></div>
          <div class="item_box1"></div>
          <div class="item_box1"></div>
          <div class="item_box1"></div>
          <div class="item_box1"></div>
          <div class="item_box1"></div>
          <div class="item_box1"></div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Monday" v-on:click="item.course_name?schedule_Monday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Tuesday" v-on:click="item.course_name?schedule_Tuesday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Wednesday" v-on:click="item.course_name?schedule_Wednesday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Thursday" v-on:click="item.course_name?schedule_Thursday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>

        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Friday" v-on:click="item.course_name?schedule_Friday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Saturday" v-on:click="item.course_name?schedule_Saturday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
        <div class="list">
          <div class="title"></div>
          <div class="item_box" v-for="(item, index) in schedule_Sunday" v-on:click="item.course_name?schedule_Sunday_alert(index):null">
            <p>{{item.course_name.length>5?item.course_name.substring(0,5)+"...":item.course_name}}</p>
            <p>{{item.teacher_name}}</p>
            <p>{{item.classroom}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import {timeTable} from "../../axios/api"
    export default {
      name: "schedule_query",
      data(){
        return{
          search_show:true,
          options: [{value: 1, label: ''}, {value: 2, label: ''}, {value: 3, label: ''}, {value: 4, label: ''}, {value: 5, label: ''}, {value: 6, label: ''}, {value: 7, label: ''}, {value: 8, label: ''}, {value: 9, label: ''}, {value: 10, label: ''}, {value: 11, label: ''}, {value: 12, label: ''}, {value: 13, label: ''}, {value: 14, label: ''}, {value: 15, label: ''}, {value: 16, label: ''}, {value: 17, label: ''}, {value: 18, label: ''}, {value: 19, label: ''}, {value: 20, label: ''}],
          value: '',
          alert_ctrl:false,
          loading: false,
          return_cache:'',
          alert_item_cache:[],
          alert_item:[],
          schedule_Monday:[],
          schedule_Tuesday :[],
          schedule_Wednesday:[],
          schedule_Thursday:[],
          schedule_Friday:[],
          schedule_Saturday:[],
          schedule_Sunday:[],
        }
      },
      methods:{
        show_alert(){        //
          const h = this.$createElement;
          for(let i in this.alert_item_cache){
            this.alert_item.push(h('p',null,this.alert_item_cache[i]));
          }
          this.$msgbox({
            title: '',
            message: h('div',null, this.alert_item),
            showCancelButton: false,
          }).then(action => {
            //
          });
          this.alert_item=[];
          this.alert_item_cache=[]
        },
        schedule_Monday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Monday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Monday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Monday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Tuesday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Tuesday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Tuesday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Tuesday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Wednesday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Wednesday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Wednesday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Wednesday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Thursday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Thursday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Thursday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Thursday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Friday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Friday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Friday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Friday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Saturday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Saturday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Saturday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Saturday[index].teacher_name+'\t');
          this.show_alert();
        },
        schedule_Sunday_alert(index){
          this.alert_item_cache.push(''+this.schedule_Sunday[index].course_name+'\t');
          this.alert_item_cache.push(''+this.schedule_Sunday[index].classroom+'\t');
          this.alert_item_cache.push(''+this.schedule_Sunday[index].teacher_name+'\t');
          this.show_alert();
        },
        pickerChange(e){
          console.log(e);
          this.search_show = false;
        },
        findTimeTable(){          //
          //
          /*
             this.$message({
             type: 'info',
             message: ''
             });
           */
          const that = this;

          this.loading = true;      //
          /*
            setTimeout(() => {
            this.loading = false;
            }, 1000);
          */

          timeTable(160121701,that.value).then(res=>{ //
            //
            // console.log(res);
            console.log(res.data.length);
            console.log(res.data[0].length);
            // console.log(res.data[1].length);
            // console.log(res.data[0][1].chapter);

            //
            that.schedule_Monday.splice(0,that.schedule_Monday.length);
            that.schedule_Tuesday.splice(0,that.schedule_Tuesday.length);
            that.schedule_Wednesday.splice(0,that.schedule_Wednesday.length);
            that.schedule_Thursday.splice(0,that.schedule_Thursday.length);
            that.schedule_Friday.splice(0,that.schedule_Friday.length);
            that.schedule_Saturday.splice(0,that.schedule_Saturday.length);
            that.schedule_Sunday.splice(0,that.schedule_Sunday.length);

            //
            /*
            chapter: "3"
            classroom: "2-903"
            course_id: "6"
            course_name: "liangzilixue"
            days: "1"
            department: "li"
            semester: "2018-2019"
            student_class: "1601217"
            student_id: "160121701"
            teacher_name: "yanghui"
            weeks: "1"
            */

            that.alert_ctrl = res.data[0].length === 0;//
            that.search_show=true;//


            //
            for(let i=0; i<6; i++){
              that.schedule_Monday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Tuesday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Wednesday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Thursday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Friday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Saturday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
              that.schedule_Sunday.push({chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""});
            }

            //
            //chapter:"",classroom:"",course_id:"",course_name:"",days:"",department:"",semester:"",student_class:"",student_id:"",teacher_name:"",weeks:""


            //
            for(let i=0; i<res.data.length; i++){
              for (let j=0;j<res.data[i].length;j++){
                switch (res.data[i][j].days) {//
                  case "1": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Monday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Monday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Monday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Monday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Monday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Monday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "2": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Tuesday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Tuesday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Tuesday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Tuesday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Tuesday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Tuesday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "3": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Wednesday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Wednesday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Wednesday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Wednesday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Wednesday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Wednesday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "4": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Thursday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Thursday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Thursday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Thursday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Thursday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Thursday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "5": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Friday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Friday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Friday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Friday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Friday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Friday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "6": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Saturday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Saturday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Saturday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Saturday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Saturday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Saturday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }

                  case "7": {
                    switch (res.data[i][j].chapter) {//
                      case "1": //
                        that.schedule_Sunday[0]=res.data[i][j];
                        break;
                      case "2": //
                        that.schedule_Sunday[1]=res.data[i][j];
                        break;
                      case "3": //
                        that.schedule_Sunday[2]=res.data[i][j];
                        break;
                      case "4": //
                        that.schedule_Sunday[3]=res.data[i][j];
                        break;
                      case "5": //
                        that.schedule_Sunday[4]=res.data[i][j];
                        break;
                      case "6": //
                        that.schedule_Sunday[5]=res.data[i][j];
                        break;
                    }
                    break;
                  }
                }
              }
            }
            that.loading = false; //
          })
        },

      }
    }

</script>

<style scoped>

  .error {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .error >div {
    margin-bottom: 30px;
    font-size: 30px;
    font-weight: lighter;
  }

  .error img {
    height:500px;
  }
  .index,.allPage,.error{
    width:100%;
    height:100%;
  }
.top {
  margin-top: 30px;
  height:50px;
  width:100%;
  padding-left: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.cen{
