  flex-direction: column;
  margin-right: 30px;
}

.item_box {
  height: 60px;
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 20px;
  border: 1px #989898 solid;
}

.item_box>p{
  margin:0 auto;
  overflow: hidden;
  display: inline;
  height:20px;
}
.item_box1 {
  height: 60px;
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 22px;
}

.title {
  height:30px;
}
</style>


