<template>
  <div class="bigbox">
    <div  class="header"></div>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.teacher_name"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.teacher_id" ></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="form.teacher_department" placeholder="" style="width:326px;">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.teacher_major"></el-input>
      </el-form-item>



      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="form.teacher_join_time" style="width:326px;"></el-date-picker>
        </el-col>
      </el-form-item>

    </el-form>
    <div>
      <el-button type="primary" @click="testBeforeSubmit(form)"></el-button>
    </div>


  </div>
</template>

<script>
  import {submitTeacher} from "../../axios/api"

    export default {
        name: "addTeacher",
      data()
      {
        return{
          form:{
            teacher_name:'',
            teacher_id:'',
            teacher_department:'',
            teacher_major:'',
            teacher_join_time:''

          }
        }
      },

      //
      mounted(){
        console.log("mountedDone")
        console.log(this.form)
      },
      methods:{

        //
        testBeforeSubmit(form) {

          if (this.form.teacher_name === "") {
            alert("");return
          }if (this.form.teacher_id === "") {
            alert("");return
          }if (this.form.teacher_department === "")
          {alert("");return
          }if (this.form.teacher_major === "") {
            alert("");return
          }if (this.form.teacher_join_time === "") {
            alert("");return
          }else {
            this.submitTeachertTo(form)
          }
        },

        //
        submitTeachertTo(form) {
          submitTeacher(form).then(submitresult => {
            console.log(submitresult)
            if(submitresult.length>0){
              alert("")
            }
          }).catch(error => {
            console.log(error.response)
          })
        },

      }

    }
</script>

<style scoped>

  .bigbox {
    max-width: 1000px;

  }
  .header{
    height: 25px;
    margin: 10px;
    font-size: larger;
  }
  .nextHeader{
    height: 25px;
    margin-top: 50px;
    font-size: larger;
  }
</style>



