<template>
  <div class="main">
    <header>
    <div class="top">
      <p> ---</p>
      <el-button @click="goToLoginPage"></el-button>
    </div>
    <footer>
      <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        &nbsp;:&nbsp;{{admin_name}}&nbsp;&nbsp;&nbsp;&nbsp;
        &nbsp;:&nbsp;{{admin_id}}</p>
    </footer>
  </header>
    <div class="kongbai"></div>
    <aside>
      <el-button class="sidebarbox" @click="changeIsShow('addstudent')" style="width:50%;min-width:160px;margin:10px 0;" ><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('addteacher')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('amentmsg')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
      <el-button class="sidebarbox" @click="changeIsShow('addstuonce')" style="width:50%;min-width:160px;margin:10px 0;"><li></li></el-button>
    </aside>
    <div class="mainbox">
      <div v-if="type.length===0" >
          <div class="school_news" ></div>
          <div class="person_news" ></div>
      </div>
      <!---->
      <addStudent v-if="type==='addstudent'"></addStudent>
      <addTeacher v-if="type==='addteacher'"></addTeacher>
      <amentMsg v-if="type==='amentmsg'"></amentMsg>
      <addStuOnce v-if="type==='addstuonce'"></addStuOnce>
    </div>
  </div>
</template>

<script>

  import addStudent from './addStudent'
  import addTeacher from './addTeacher'
  import amentMsg from './amentMsg'
  import addStuOnce from './addStuOnce'

    export default {
        name: "adminPage",

      data(){
          return{
            admin_id:"",
            admin_name:"",
            usertype: "",
            type:""
          }
      },
      //
      mounted() {
          console.log("creatdone");
          this.adminMsg();
          },

      //
      components:{
        addStudent:addStudent,
        addTeacher:addTeacher,
        amentMsg:amentMsg,
        addStuOnce:addStuOnce,
      },

      methods: {

          //
          adminMsg(){
            const result = JSON.parse(localStorage.getItem('result'));
            console.log(result);
            this.admin_id = result.data[0].admin_id;
            this.admin_name = result.data[0].admin_name;
          },

        //
        changeIsShow(compoment_name){
          this.type = compoment_name
        },

        //
        goToLoginPage() {
          if (confirm("")) {
            this.$router.push({path: '/'})
          }
          else {
            return
          }
        },

      }
    }
</script>

<style scoped>
  @import "../main_style.css";
</style>



